}/*----ANIMATIONS----*//*----HERO----*/.hero {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-items: center;
  align-content: center;
  overflow: hidden;
  z-index: 800;
  height: 100vh;
  width: 100%;
  padding: 0 25px;
  background-color: #070707;
  background-size: cover;
  background-repeat: no-repeat;
  animation: notShow 1s ease-in-out;
  animation-fill-mode: forwards;
  animation-delay: 5.4s;
  will-change: height;
}

.hero__wrapper {
  position: absolute;
  height: 100vh;
  width: 100%;
  padding: 0 25px;
  top: 0;
  left: 0;
  will-change: translate;
  animation: translateTop 1s ease-in-out;
  animation-fill-mode: forwards;
  animation-delay: 5.4s;
}

.hero__background {
  position: absolute;
  top: 50vw;
  left: 12vh;
  width: 60vw;
  animation-name: spin;
  animation-duration: 60s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}/*----SVG----*/.intro {
  position: absolute;
  bottom: -50px;
  -webkit-animation: intro 3s ease-in-out;
  animation: intro 3s ease-in-out;
  animation-fill-mode: forwards;
  animation-delay: 0s;
}

svg.intro {
  top: 40%;
  left: 50%;
  z-index: 2;
  width: 95%;
  max-width: 800px;
  background-color: #0c0c0c;
  transform: translate(-50%, -50%);
  -webkit-animation: intro 3s ease-in-out;
  animation: intro 3s ease-in-out;
  animation-fill-mode: forwards;
  animation-delay: 0s;
}

svg.intro .text {
  display: none;
}

svg.intro.go {
  background-color: transparent;
}

svg.intro.go .text {
  display: block;
  font-family: 'Cuprum', sans-serif;
  font-size: 20px;
  text-transform: uppercase;
}

svg.intro.go .text-stroke {
  stroke-width: 2.5px;
  stroke-dashoffset: -900;
  stroke-dasharray: 900;
  fill: none;
  stroke: white;
  stroke-linecap: butt;
  stroke-linejoin: round;
  animation: dash 1.8s ease-in-out forwards;
}

svg.intro.go .text-stroke:nth-child(2) {
  animation-delay: .2s;
}

svg.intro.go .text-stroke:nth-child(3) {
  animation-delay: .8s;
}

svg.intro.go .text-stroke-2 {
  stroke: white;
  animation-delay: 2.1s;
}

svg.intro.go .text-stroke:nth-child(5) {
  animation-delay: 2.1s;
}

svg.intro.go .text-stroke:nth-child(6) {
  animation-delay: 2.7s;
}

