.header__navigation {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: black;
 /*----NAVIGATION-INLINE-WRAPPER----*/;
}

.header__navigation__burger {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
 /*----NAVIGATION-ACTIVE-WRAPPER----*/;
}

.header__navigation__burger-checkbox {
  display: none;
}

.header__navigation__burger-checkbox:checked ~ .navigationBackground {
  transform: scale(80);
}

.header__navigation__burger-checkbox:checked ~ .navigationWrapper {
  opacity: 1;
  width: 100vw;
}

.header__navigation__burger-checkbox:checked + .navigationButton {
  background-color: transparent !important;
}

.header__navigation__burger-checkbox:checked + .navigationButton .navigationIcon {
  background-color: transparent;
}

.header__navigation__burger-checkbox:checked + .navigationButton .navigationIcon::before {
  top: 0;
  transform: rotate(135deg);
}

.header__navigation__burger-checkbox:checked + .navigationButton .navigationIcon::after {
  top: 0;
  transform: rotate(-135deg);
}

.header__navigation__burger-button {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
  height: 60px;
  width: 60px;
  margin-right: 15px;
  background-color: #252525;
  text-align: center;
  color: white;
  cursor: pointer;
}

.header__navigation__burger-button:hover .navigationIcon::before {
  top: -1rem;
}

.header__navigation__burger-button:hover .navigationIcon::after {
  top: 1rem;
}

.header__navigation__burger-button-icon {
  position: relative;
}

.header__navigation__burger-button-icon::before {
  top: -.8rem;
}

.header__navigation__burger-button-icon::after {
  top: .8rem;
}

.header__navigation__burger-button-icon,.header__navigation__burger-button-icon::before,.header__navigation__burger-button-icon::after {
  display: inline-block;
  width: 3rem;
  height: 2px;
  background-color: #fff;
}

.header__navigation__burger-button-icon::before,.header__navigation__burger-button-icon::after {
  content: "";
  position: absolute;
  left: 0;
  transition: all .2s;
}

