@keyframes notShow {
  0% {
    height: 100vh;
    opacity: 1;
  }

  100% {
    height: 0vh;
    opacity: 0.5;
  }
}

@keyframes translateTop {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(-100vh);
  }
}

@keyframes intro {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes spin {
  0% {
    opacity: 0;
    transform: rotate(0deg);
  }

  3% {
    opacity: 1;
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes showJob {
  0% {
    opacity: 0;
    height: 0;
  }

  100% {
    opacity: 1;
    height: 23px;
  }
}

@keyframes showHeader {
  0% {
    height: 0;
    top: -90px;
  }

  100% {
    height: 60px;
    display: flex;
    top: 0;
  }
}

@keyframes expande {
  0% {
    width: 60px;
    height: 60px;
  }

  100% {
    width: 200%;
    height: 200%;
  }
}

@keyframes spin {
  0% {
    opacity: 0;
    transform: rotate(0deg);
  }

  3% {
    opacity: 1;
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes opacity-appear {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }

  80% {
    transform: translateX(1rem);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}
